import React from 'react';
import { useContext, useState, useEffect } from 'react';
import TrackVisibility from 'react-on-screen';
import Carousel from 'react-multi-carousel';
import './Work.css';
import 'react-multi-carousel/lib/styles.css';
import { ThemeContext } from '../../Theme';

function Work() {

  // From Theme.js
  const { headerHeight } = useContext(ThemeContext); //header height hook

  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 1024 },
      items: 4,
    },    
    desktop: {
      breakpoint: { max: 1023, min: 992 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 991, min: 576 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 575, min: 0 },
      items: 1,
    }
  };

  const [modalData, setModalData] = useState({ id: '', thumb: '', url: '' });
  const [showModal, setShowModal] = useState(false);
  const [fadeIn, setFadeIn] = useState(false); // For custom fade-in

  const images = [
    { id: 1, thumb: 'https://placehold.co/320x240/EEE/31343C', url: 'https://placehold.co/1024x768/EEE/31343C' },
    { id: 2, thumb: 'https://placehold.co/320x240/EEE/31343C', url: 'https://placehold.co/1024x768/EEE/31343C' },
    { id: 3, thumb: 'https://placehold.co/320x240/EEE/31343C', url: 'https://placehold.co/1024x768/EEE/31343C' },
    { id: 4, thumb: 'https://placehold.co/320x240/EEE/31343C', url: 'https://placehold.co/1024x768/EEE/31343C' },
    { id: 5, thumb: 'https://placehold.co/320x240/EEE/31343C', url: 'https://placehold.co/1024x768/EEE/31343C' },
  ];

  const handleImageClick = (id, url) => {
    setModalData({ id, url });
    setShowModal(true);
    document.body.classList.add('modal-open');
  };

  const handleCloseModal = () => {
    setFadeIn(false); // Start fade-out
    setTimeout(() => {
      setShowModal(false); // Hide modal after fade-out is complete
    }, 300); // Match the fade-out transition duration
    document.body.classList.remove('modal-open');
  };  

  // Add fade-in effect after modal is opened
  useEffect(() => {
    if (showModal) {
      setTimeout(() => {
        setFadeIn(true);
      }, 10); // Slight delay to trigger fade-in
    }
  }, [showModal]);

  return (    
    <>
      <div className='section-heading work-heading'>
        <div className='container-xxl'>
          <TrackVisibility partialVisibility>
            {({isVisible}) =>        
              <h2 className={`h1 m-0 ${isVisible ? 'animate__animated animate__fadeInUpBig' : 'animate__animated animate__fadeOutDownBig'}`}>Work</h2>
            }
          </TrackVisibility>
        </div>
      </div>           
      <div className='work-container flex-fill' style={{paddingBottom: headerHeight}}>
        <div className='container-xxl'>
          <div className='work-wrapper d-flex flex-column'>  
            <div className='mt-auto'>
              <TrackVisibility>
                {({isVisible}) =>  
                  <div className={isVisible ? 'animate__animated animate__fadeIn' : 'animate__animated animate__fadeOut'}>
                    <h3 className='mb-2'>Designs that Inspire, Solutions that Elevate</h3>
                    <p className='lead'>Explore a curated selection of my projects that showcase my skills and creativity. From innovative design solutions to impactful branding initiatives, each piece reflects my passion and dedication to delivering exceptional results. Click on the images to view more about the process and inspiration behind each project!</p>
                  </div>
                }
              </TrackVisibility>  
            </div>                      
            <Carousel responsive={responsive} infinite={true} className='work-slider mt-auto mb-auto pb-5'>
              {images.map((image) => (
                <img
                  key={image.id}
                  src={image.thumb}
                  alt={image.id}
                  className='img-fluid'
                  style={{ width: '100%', cursor: 'pointer' }}
                  onClick={() => handleImageClick(image.id, image.url)}
                />
              ))}                
            </Carousel>
          </div>
        </div>
      </div>
      <span className='page position-absolute'>
        <TrackVisibility offset={200}>
          {({isVisible}) =>
            <div className={`number position-relative ${isVisible ? 'animate__animated animate__fadeInLeftBig' : 'animate__animated animate__fadeOutLeftBig'}`} data-id='4'></div>
          }
        </TrackVisibility>
      </span> 

      {showModal && (
        <div className={`modal ${fadeIn ? 'fade-in' : 'fade-out'}`} style={{ display: 'block', opacity: fadeIn ? 1 : 0 }} tabIndex="-1" role="dialog">
          <div className="modal-dialog modal-fullscreen" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Image Details | <small>Data ID: {modalData.id}</small></h5>
                <button type="button" className="display-6 close text-white" onClick={handleCloseModal}>
                  <span>&times;</span>
                </button>
              </div>
              <div className="modal-body position-relative">
                <img src={modalData.url} alt={modalData.id} style={{ maxWidth: '100%' }} className='position-absolute top-50 start-50 translate-middle img-fluid' />
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary btn-lg" onClick={handleCloseModal}>
                  Close
                </button>
              </div>
            </div>
          </div>
          <div className="modal-backdrop fade show" onClick={handleCloseModal}></div>
        </div>
      )}
    </>
  )
}

export default Work